


:root {
    --gb-color: red;
    --fff: #fff;
    --blur-fff: rgba(255, 255, 255, 0.699);
    --000: #000;
    --border-color: rgba(231, 223, 223, 0.842);
    --rgbaBG: rgba(0, 0, 0, 0.322);
    --boxShadow: rgba(110, 107, 107, 0.575);
    --avzal-cardBG: #949191;
    --kulrang: #f3f4f5;
    --link-color: rgb(99, 98, 98);
}




footer
{
    padding: 30px 4%;
    width: 100%;
    height: auto;
}

.footer-box
{
    display: flex;
}

.footer-box div:first-child
{
    margin: 0 0;
}

.footer-box div
{
    margin: 0 80px;
}



.footer-box a
{
    color: var(--link-color);
    display: flex;
    align-items: center;
    margin: 10px 0;
    font-weight: 600;
}

.footer-box .hamkorlar-info a:hover,
.footer-box .sections-info a:hover
{
    text-decoration: underline; 
    color: var(--gb-color);
}


    
.contact-info a svg
{
    font-size: 1em;
    width: 35px;
    height: 40px;
    margin-right: 10px;
    color: red;
    display: flex;
    align-items: center;
    justify-content: center;
}


.footer-copyryter
{
    position: relative;
    margin-top: 20px;
    width: 100%;
    height: 10vh;
    color: var(--gb-color);
    display: flex;
    align-items: center;
    border-top: 1px solid rgba(0, 0, 0, 0.418);
    font-weight: 600;
}


.btn-up
{
    position: absolute;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0%;
    left: 37%;
    transform: translate(-50%,-50%);
    border: 1px solid var(--rgbaBG);
    border-radius: 5px;
    font-size: 2em;
    background: var(--fff);
    cursor: pointer;
    transition: .5s;
}


.btn-up:hover
{
    background: var(--gb-color);
    color: var(--fff);
    border-color: var(--fff);
}














@media (max-width: 870px) {

    .footer-box div
    {
        margin: 0 30px;
    }

    .footer-box h1
    {
        font-size: 1.2em;
    }

    


}








@media (max-width: 520px) {
    .footer-box
    {
        display: flex;
        flex-direction: column;
    }

    .footer-box div
    {
        margin: 20px 0;
    }



 

    
}